// React
import React from 'react';
import './ChatContainer.css';


const MessageContainer = (props) => (
    <div>
        <ul className="chat-container">
            {props.messages.map((msg) => (
                <li>
                    {msg}
                </li>
            ))}
        </ul>
    </div>
);



export default MessageContainer;



